/*
font-family: 'Lato', sans-serif;
font-family: 'PT Sans', sans-serif;
*/
body {
  margin: 0;
  background-color: #fff;
  font-family: 'PT Sans', sans-serif;
  font-size: 15px;
  line-height: 1.5;
  color: #585858; }

*,
*:before,
*:after {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

p {
  margin: 0 0 10px; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

.img {
  display: block;
  max-width: 100%;
  height: auto; }

.container {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px; }

.header {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000; }
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 85px; }

.nav {
  display: flex;
  height: 100%;
  font-size: 15px; }
  .nav__link {
    display: flex;
    align-items: center;
    padding: 0 15px;
    color: #fff;
    text-decoration: none; }
  .nav__link.active {
    background-color: #004AF2; }

.intro {
  margin-bottom: 30px;
  background: #000 url("../../img/intro-bg.jpg") no-repeat center;
  background-size: cover; }
  .intro__inner {
    height: 600px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
  .intro__text {
    margin-bottom: 45px;
    text-align: right; }
  .intro__countries {
    font-size: 15px;
    color: #fff; }
  .intro__title {
    position: relative;
    font-family: 'Lato', sans-serif;
    font-size: 110px;
    line-height: 0.9;
    color: rgba(255, 255, 255, 0.8);
    font-weight: bold; }
  .intro__title-amp {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 170px;
    line-height: 1;
    font-weight: 900;
    color: rgba(255, 255, 255, 0.2); }

.request-form {
  width: 100%;
  max-width: 400px;
  overflow: hidden;
  background-color: #004AF2;
  border-radius: 5px; }
  .request-form--intro {
    position: relative;
    bottom: -30px; }
  .request-form__header {
    padding: 20px 30px;
    background-color: #033FC9; }
  .request-form__title {
    font-family: 'Lato', sans-serif;
    font-size: 30px;
    font-weight: 900;
    color: #fff; }
  .request-form__text {
    font-size: 15px;
    line-height: 1.4;
    color: #fff; }
  .request-form__content {
    padding: 30px; }

.form__group {
  margin-bottom: 25px; }

.input {
  display: block;
  width: 100%;
  padding-bottom: 12px;
  padding-left: 50px;
  background: none;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  font-family: 'PT Sans', sans-serif;
  font-size: 16px;
  color: #fff;
  transition: border-color .2s linear; }
  .input::placeholder {
    color: #fff; }
  .input:focus {
    outline: none;
    border-bottom-color: #fff; }
  .input--user {
    background: url("../../img/user-icon.svg") no-repeat left 15px top 5px; }
  .input--phone {
    background: url("../../img/phone-icon.svg") no-repeat left 15px top 5px; }
  .input--email {
    background: url("../../img/mail-icon.svg") no-repeat left 15px top 6px; }

.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 15px 45px;
  font-family: 'PT Sans', sans-serif;
  font-size: 18px;
  line-height: 1.1;
  color: #fff;
  font-weight: 700;
  text-align: center;
  background: #333;
  border: 0;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer; }
  .btn--orange {
    background-image: linear-gradient(to right, #FF903E, #FF2C2C); }
  .btn--block {
    display: block;
    width: 100%; }

.section {
  padding: 60px 0; }
  .section--map {
    background: url("../../img/map.svg") no-repeat left 30px top 130px; }
  .section__title {
    margin-bottom: 40px;
    font-size: 45px;
    line-height: 1.2;
    color: #2c2c2c;
    font-weight: 900;
    font-family: 'Lato', sans-serif; }
    .section__title span {
      color: #0D53F2; }

.services {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 0 50px;
  position: relative;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.2); }
  .services__main-title {
    position: absolute;
    bottom: 12px;
    right: 30px;
    z-index: 1;
    font-family: 'Lato', sans-serif;
    font-size: 90px;
    line-height: 1;
    color: rgba(44, 44, 44, 0.05);
    font-weight: 900; }
  .services__item {
    display: flex;
    flex-direction: column;
    width: 25%;
    padding: 0 25px;
    margin-bottom: 30px;
    position: relative;
    z-index: 2; }
  .services__title {
    margin-bottom: 20px;
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    line-height: 1.2;
    color: #2C2C2C;
    font-weight: 900; }
  .services__text {
    margin-bottom: 20px;
    font-size: 15px;
    line-height: 1.4;
    color: #585858; }
  .services__footer {
    margin-top: auto; }
  .services__link {
    display: inline-block;
    font-size: 15px;
    color: #004AF2;
    text-decoration: none;
    transition: color .1s linear; }
    .services__link:after {
      content: "";
      display: block;
      width: 10px;
      height: 2px;
      margin-top: 5px;
      background-color: #004AF2;
      transition: width .1s linear; }
    .services__link:hover {
      color: #FF3E2F; }
    .services__link:hover:after {
      width: 100%;
      background-color: #FF3E2F; }

.clients {
  display: flex;
  margin-bottom: 60px; }
  .clients__logos {
    width: 60%; }
  .clients__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
    .clients__list li {
      margin-right: 40px;
      margin-bottom: 40px; }
  .clients__reviews {
    width: 40%; }
  .clients-love {
    display: inline-block;
    margin-left: 40px;
    min-height: 47px;
    padding-top: 5px;
    background: url("../../img/heart.svg") no-repeat left 19px top;
    font-size: 18px;
    color: #464646; }

.reviews {
  position: relative; }
  .reviews:before {
    content: "";
    display: block;
    width: 92px;
    height: 126px;
    background: url("../../img/lines.svg") no-repeat;
    position: absolute;
    top: -15px;
    left: 2px;
    z-index: 1; }
  .reviews__header {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    position: relative;
    z-index: 2; }
  .reviews__header-content {
    flex-grow: 1;
    padding-left: 20px;
    font-family: 'Lato', sans-serif; }
  .reviews__photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 5px solid #fff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16); }
  .reviews__name {
    font-size: 18px;
    color: #2c2c2c;
    font-weight: 900; }
  .reviews__company {
    color: #2c2c2c;
    font-size: 15px; }
  .reviews__content {
    font-size: 15px;
    line-height: 1.8;
    color: #585858; }
    .reviews__content p {
      margin-bottom: 20px; }
    .reviews__content p:first-child {
      text-indent: 20px; }

.benefits {
  display: flex; }
  .benefits__col {
    width: 50%; }

.benefits-list {
  width: 100%;
  max-width: 370px;
  padding-left: 50px;
  position: relative;
  background-image: linear-gradient(to bottom, #fff 40%, #a5a5a5 40%);
  background-size: 2px 35px;
  background-repeat: repeat-y;
  background-position: left 12px top -4px; }
  .benefits-list:before, .benefits-list:after {
    content: "";
    display: block;
    width: 26px;
    height: 36px;
    background-color: #fff;
    background-repeat: no-repeat;
    position: absolute;
    left: 0;
    z-index: 1; }
  .benefits-list:before {
    background-image: url("../../img/pin-start.svg");
    top: -5px; }
  .benefits-list:after {
    background-image: url("../../img/pin-end.svg");
    bottom: -4px; }
  .benefits-list__item:not(:last-child) {
    margin-bottom: 35px; }
  .benefits-list__title {
    margin-bottom: 12px;
    font-family: 'Lato' ,sans-serif;
    font-size: 21px;
    line-height: 1.2;
    color: #2c2c2c;
    font-weight: 900; }
  .benefits-list__text {
    font-size: 15px;
    line-height: 1.4;
    color: #585858; }
